'use client';

import { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Trash2 } from 'lucide-react';
import { Dialog, DialogContent, DialogTitle, DialogTrigger, DialogFooter, DialogClose } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import Sidebar from '@/components/sidebar';

export default function Products() {
  const [products, setProducts] = useState([
    {
      id: "KFC040",
      name: "CHOCOLATE CHIP COOKIE",
      originalPrice: 1.50,
      salePrice: null,
      riels: "6,000",
      image: "https://tse2.mm.bing.net/th/id/OIP.iwYumzFmcCIyrRJW5GRLqwHaHa?r=0&rs=1&pid=ImgDetMain&o=7&rm=3",
      badges: ["New"]
    },
    { 
      id: 'PROD001', 
      name: 'Wireless Headphones', 
      originalPrice: 99.99, 
      salePrice: 89.99, 
      riels: "400,000", 
      image: "https://example.com/headphones.jpg", 
      badges: ["Sale"] 
    },
    { 
      id: 'PROD002', 
      name: 'Smartphone', 
      originalPrice: 499.99, 
      salePrice: null, 
      riels: "2,000,000", 
      image: "https://example.com/smartphone.jpg", 
      badges: [] 
    },
  ]);

  const [newProduct, setNewProduct] = useState({
    id: '',
    name: '',
    originalPrice: '',
    salePrice: '',
    riels: '',
    image: '',
    badges: ''
  });
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  const handleAddProduct = () => {
    if (newProduct.id && newProduct.name && newProduct.originalPrice && newProduct.riels) {
      setProducts([
        ...products,
        {
          ...newProduct,
          originalPrice: parseFloat(newProduct.originalPrice),
          salePrice: newProduct.salePrice ? parseFloat(newProduct.salePrice) : null,
          badges: newProduct.badges ? newProduct.badges.split(',').map(badge => badge.trim()) : []
        }
      ]);
      setNewProduct({
        id: '',
        name: '',
        originalPrice: '',
        salePrice: '',
        riels: '',
        image: '',
        badges: ''
      });
      setIsDialogOpen(false);
    }
  };

  return (
    <div className="flex min-h-screen bg-gray-100">
      {/* Sidebar */}
      <Sidebar />

      {/* Main Content */}
      <div className="flex-1 p-6">
        <div className="flex justify-between items-center mb-6">
          <h1 className="text-3xl font-bold text-gray-800">Products</h1>
          <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
            <DialogTrigger asChild>
              <Button className="bg-blue-600 text-white hover:bg-blue-700">
                Add Product
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[425px]">
              <DialogTitle>Add New Product</DialogTitle>
              <div className="grid gap-4 py-4">
                <div className="grid grid-cols-4 items-center gap-4">
                  <label htmlFor="id" className="text-right text-sm font-medium text-gray-700">ID</label>
                  <Input
                    id="id"
                    value={newProduct.id}
                    onChange={(e) => setNewProduct({ ...newProduct, id: e.target.value })}
                    className="col-span-3"
                    placeholder="e.g., PROD003"
                  />
                </div>
                <div className="grid grid-cols-4 items-center gap-4">
                  <label htmlFor="name" className="text-right text-sm font-medium text-gray-700">Name</label>
                  <Input
                    id="name"
                    value={newProduct.name}
                    onChange={(e) => setNewProduct({ ...newProduct, name: e.target.value })}
                    className="col-span-3"
                    placeholder="e.g., Product Name"
                  />
                </div>
                <div className="grid grid-cols-4 items-center gap-4">
                  <label htmlFor="originalPrice" className="text-right text-sm font-medium text-gray-700">Original Price</label>
                  <Input
                    id="originalPrice"
                    type="number"
                    value={newProduct.originalPrice}
                    onChange={(e) => setNewProduct({ ...newProduct, originalPrice: e.target.value })}
                    className="col-span-3"
                    placeholder="e.g., 99.99"
                  />
                </div>
                <div className="grid grid-cols-4 items-center gap-4">
                  <label htmlFor="salePrice" className="text-right text-sm font-medium text-gray-700">Sale Price</label>
                  <Input
                    id="salePrice"
                    type="number"
                    value={newProduct.salePrice}
                    onChange={(e) => setNewProduct({ ...newProduct, salePrice: e.target.value })}
                    className="col-span-3"
                    placeholder="e.g., 89.99 (optional)"
                  />
                </div>
                <div className="grid grid-cols-4 items-center gap-4">
                  <label htmlFor="riels" className="text-right text-sm font-medium text-gray-700">Riels</label>
                  <Input
                    id="riels"
                    value={newProduct.riels}
                    onChange={(e) => setNewProduct({ ...newProduct, riels: e.target.value })}
                    className="col-span-3"
                    placeholder="e.g., 400,000"
                  />
                </div>
                <div className="grid grid-cols-4 items-center gap-4">
                  <label htmlFor="image" className="text-right text-sm font-medium text-gray-700">Image URL</label>
                  <Input
                    id="image"
                    value={newProduct.image}
                    onChange={(e) => setNewProduct({ ...newProduct, image: e.target.value })}
                    className="col-span-3"
                    placeholder="e.g., https://example.com/image.jpg"
                  />
                </div>
                <div className="grid grid-cols-4 items-center gap-4">
                  <label htmlFor="badges" className="text-right text-sm font-medium text-gray-700">Badges</label>
                  <Input
                    id="badges"
                    value={newProduct.badges}
                    onChange={(e) => setNewProduct({ ...newProduct, badges: e.target.value })}
                    className="col-span-3"
                    placeholder="Comma-separated, e.g., New,Sale"
                  />
                </div>
              </div>
              <DialogFooter>
                <DialogClose asChild>
                  <Button variant="outline" className="text-gray-600 hover:text-gray-800">
                    Cancel
                  </Button>
                </DialogClose>
                <Button onClick={handleAddProduct} className="bg-blue-600 text-white hover:bg-blue-700">
                  Save Product
                </Button>
              </DialogFooter>
            </DialogContent>
          </Dialog>
        </div>
        <Card className="shadow-lg">
          <CardHeader>
            <CardTitle className="text-xl">Product List</CardTitle>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead className="text-gray-700">Product ID</TableHead>
                  <TableHead className="text-gray-700">Image</TableHead>
                  <TableHead className="text-gray-700">Name</TableHead>
                  <TableHead className="text-gray-700">Original Price</TableHead>
                  <TableHead className="text-gray-700">Sale Price</TableHead>
                  <TableHead className="text-gray-700">Riels</TableHead>
                  <TableHead className="text-gray-700">Badges</TableHead>
                  <TableHead className="text-gray-700">Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {products.map((product) => (
                  <TableRow key={product.id} className="hover:bg-gray-50">
                    <TableCell>{product.id}</TableCell>
                    <TableCell>
                      <img 
                        src={product.image} 
                        alt={product.name} 
                        className="w-12 h-12 object-cover rounded"
                      />
                    </TableCell>
                    <TableCell>{product.name}</TableCell>
                    <TableCell>${product.originalPrice.toFixed(2)}</TableCell>
                    <TableCell>
                      {product.salePrice ? `$${product.salePrice.toFixed(2)}` : '-'}
                    </TableCell>
                    <TableCell>{product.riels}</TableCell>
                    <TableCell>
                      {product.badges.length > 0 ? (
                        product.badges.map((badge, index) => (
                          <Badge key={index} className="mr-1">
                            {badge}
                          </Badge>
                        ))
                      ) : (
                        '-'
                      )}
                    </TableCell>
                    <TableCell className="flex gap-2">
                      <Button variant="outline" size="sm" className="text-blue-600 hover:text-blue-800">
                        Edit
                      </Button>
                      <Button variant="outline" size="sm" className="text-red-600 hover:text-red-800">
                        <Trash2 className="w-4 h-4" />
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}